

















import { Component, Vue } from 'vue-property-decorator'

@Component
export default class FormId extends Vue {
	/**
	 * @LastEditors: chuyinlong
	 * @description: 提交表单
	 * @param {string | number} e
	 * @returns {*}
	 */

	getFormId(e: { detail: { formId: string | number } }) {
		this.$emit('click', {
			formId: e.detail.formId
		})
	}
}
